import React from 'react';

const Pagination = ({ todosPerPage, totalTodos, paginate }) => {
    const pageNumbers = [];
    for (let i = 1; i <= (Math.ceil(totalTodos / todosPerPage)); i++) {
        pageNumbers.push(i);
    }

    return (
        <div>
            <div className="mainSelect">
                {totalTodos > 0 ?
                    <div className="inSelect">
                        <button onClick={() => { paginate(1) }} className="selectArrow">‚ùÆ</button>
                        {pageNumbers.map((number => (
                            <div key={number} select>
                                <button onClick={() => paginate(number)} href="!#" className="select">
                                    {number}
                                </button>
                            </div>
                        )))}
                        <button onClick={() => { paginate(Math.ceil(totalTodos / todosPerPage)) }} className="selectArrow">‚ùØ</button>
                    </div>
                    : ""}
            </div>
        </div>
    )
}

export default Pagination;